@charset "utf-8";
@font-face {
    font-family: 'Ownglyph_ParkDaHyun';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2411-3@1.0/Ownglyph_ParkDaHyun.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}
* {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
a {
    color: inherit;
    text-decoration: none;
}
body {
    font-family: 'Ownglyph_ParkDaHyun';
    color: #333;
}
.header .inner {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
    margin-bottom: 50px;
    text-align: center;
    margin-top: 50px;
}
.header .inner h1 {
    margin-bottom: 20px;
    font-size: 50px;
}
.header .inner span {
    font-size: 20px;
}
.check {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
}
.check form {
    position: relative;
}
.check form input[type="text"] {
    width: 100%;
    height: 50px;
    border: 2px solid #333;
    border-radius: 30px;
    padding: 0 10px;
}
.check form button {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 0;
    top: 10px;
    border: 0;
    background-color: transparent;
}
.check form button img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.check_list {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
}
.check_list .item {
    width: 100%;
    height: 80px;
    background-color: #ffefc4;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
}
.check_list .item strong {
    margin-left: 30px;
    font-size: 25px;
    font-weight: normal;
}
.check_list .item button {
    width: 25px;
    height: 25px;
    border: 0;
    background-color: transparent;
    margin-right: 30px;
}
.check_list .item button img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}